.portfolio {
    background-color: #87BBA2;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1{
        font-size: 50px;
    }
}

.container{
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;

    .item{
        width: 420px;
        height: 150px;
        border-radius: 20px;
        border: 1px solid white;
        margin: 10px 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        transition: all 1s ease;
        cursor: pointer;

        h3{
            position: absolute;
            font-size: 20px;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            z-index: 1;
        }

        &:hover{
            background-color: seagreen;
            img{
                opacity: 0.2;
                z-index: 0;
            }
        }
    }
}